  /* projects */
  .myDiv {
    box-shadow: none;
    
  }

  .cards-section .myDesc {
    display: block;
    width: 85%;
    margin: auto;
    text-align: center;


  }

  .card {
    width: 50%;
    perspective: 70em;
    padding: 1em 2em;
    margin: auto;
    margin-top: 1em;
    text-align: center;
  }

  .card h4 {
    font-size: 2.6em;
    color: #181617;
  }

  .card-container {
    display: grid;
    grid-template-columns: 1fr;
    margin: auto;
    margin-bottom: 0;
    justify-content: center;
    transition: all .6s ease-in-out;
    transform-style: preserve-3d;
    text-align: center;

  }

  .card-front {
    grid-area: 1 / 1;
  }

  .card-back {
    grid-area: 1 / 1;
    background-color: #F9F9F9;
    transform: rotateX(-90deg);
    display: grid;
    place-items: center;
    color: #4F4D47;
    text-align: center;
  }


  .card-back h4 {
    font-weight: bold;
    text-align: center;
  }

  .card-back p {
    display: block;
    width: 10em;
    text-align: center;
  }

  .card-back a{
    margin-top: 2em;
    color: #4F4D47;
  }

  .card-back a:hover {
    color: #4F4D47;
  }

  .card-container img {
    width: 15em;
    height: 10em;
  }

  .card:hover .card-container {
    transform: rotateX(90deg);
  }

  /* card end */

  /* contact */

  .contact-form form {
    display: grid;
    grid-template-columns: 1fr;
    width: 50%;
    margin: 1.5em auto;
  }

  .contact-form {
    border-radius: 4px;
    position: relative;
    background-color: #f9f9f9;
    transition: 0.3s all;
  }
  .contact-form:hover {
    background-color: #F9F9F9;
  }

  .contact-form form > input,
