    color: #353434;
    width: 6em;
    background-color: #F9F9F9;
    padding: 0.3em 0.8em;
    border: none;
    margin: 2em auto;
    text-transform: uppercase;
    font-size: 1.2em;
    border-radius: 0.3em;

    transition: all 0.5s ease;
  }

  .submit-btn:hover {
    background-color: #F9F9F9;
    color: #393a3a;
  }

  .send-icon {
    margin-bottom: -0.17em;
    padding-left: 0.2em;
    font-size: 1.1em;
  }

  .my_footer {
    display: grid;
    place-items: center;
    color: #fff;
    padding: 7em;
    font-weight: bold;
  }

  .more-footer {
    display: grid;
    grid-template-columns: 1fr;
    text-align: center;
  }

  .more-footer a {
    text-decoration: none;
    color: #F9F9F9;
    padding: 0.4em;
  }

  .fade-in {
    opacity: 0;
    transition: opacity 250ms ease-in;
  }

  .fade-in.appear {
    opacity: 1;
  }
}

